<template>
  <div class="wrapperCenter">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item
        >来粉统计 / 控制台 / 转化分析 / 渠道分析</el-breadcrumb-item
      >
    </el-breadcrumb>

    <div class="search">
      <div>
        <el-radio v-model="radio" label="1">近3天</el-radio>
        <el-radio v-model="radio" label="2">近7天</el-radio>
        <el-radio v-model="radio" label="3">近30天</el-radio>
      </div>
      <el-date-picker
        class="dateSpace"
        v-model="dateSpace"
        type="daterange"
        value-format="yyyy-MM-dd"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
      ></el-date-picker>
      <el-select
        v-model="search.site_id"
        placeholder="请选择网站"
        class="searchDomain"
      >
        <el-option
          v-for="item in domainArr"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        >
        </el-option>
      </el-select>
      <el-button class="searchBtn" @click="searchFn" type="primary"
        >搜索</el-button
      >
    </div>
    <div id="lineChart" v-if="tableData.length > 0"></div>

    <el-table
      :data="tableData"
      style="width: 100%"
      :header-cell-style="{ background: '#F3F4F7', color: '#555' }"
    >
      <el-table-column
        type="index"
        label="序号"
        width="100px"
        :index="indexhandel"
      ></el-table-column>
      <el-table-column
        prop="channel_name"
        label="渠道来源"
        width="180"
      ></el-table-column>
      <el-table-column prop="ip_num" label="IP数" width="180"></el-table-column>
      <el-table-column prop="uv_num" label="UV数"></el-table-column>
      <el-table-column prop="pv_num" label="浏览量(PV)"></el-table-column>
      <el-table-column prop="change_num" label="转化数"></el-table-column>
      <div slot="empty" class="nodata">
        <img src="@/assets/noHaveData.jpg" alt="" />
        <p>暂无数据</p>
      </div>
    </el-table>
    <el-pagination
      v-if="tableData.length > 0"
      :page-size="search.page_size"
      :current-page.sync="search.page"
      background
      layout="prev, pager, next"
      :total="total"
      @current-change="currentChange"
    ></el-pagination>
  </div>
</template>

<script>
import utils from "@/mixin/watchDate.js";
import * as echarts from "echarts";
export default {
  mixins: [utils],
  data() {
    return {};
  },
  methods: {
    setChart(type, dataArr) {
      let se = [];
      let dateDataArr = dataArr[0]
        ? dataArr[0].chart_data.map((e) => {
            return e.date_time;
          })
        : [];
      if (dateDataArr.length < 1) {
        return;
      }
      dataArr.forEach((e) => {
        let obj = {
          data: e.chart_data.map((e) => e.ip_num),
          type: "line",
          smooth: true,
          name: e.channel_name,
        };
        se.push(obj);
      });
      let option = {
        xAxis: {
          type: "category",
          data: dateDataArr,
        },
        legend: {
          data: type,
        },
        yAxis: {
          type: "value",
        },
        series: se,
      };
      echarts.init(document.getElementById("lineChart")).setOption(option);
    },
    searchFn() {
      this.search.page = 1;
      this.getData();
    },
    getData() {
      this.axios
        .post("/v1/data/Channel_change/index/page", this.search)
        .then((res) => {
          if (res.data.code == 200) {
            this.tableData = res.data.result.list;
            this.total = res.data.result.page_info.total;
            this.axios
              .post("/v1/data/Channel_change/chart_data", {
                start_time: this.search.start_time,
                end_time: this.search.end_time,
              })
              .then((res) => {
                if (res.data.code == 200) {
                  let typeArr = [];
                  res.data.result.forEach((e) => {
                    typeArr.push(e.channel_name);
                  });
                  this.setChart(typeArr, res.data.result);
                }
              });
          } else {
            this.$message.error(res.data.message);
          }
        });
    },
  },
};
</script>

<style lang="less" scoped>
.searchBtn {
  margin-left: 20px;
}
.search,
.dateSpace {
  margin: 20px 0;
}
#lineChart {
  width: 100%;
  height: 260px;
}
</style>